* {
  box-sizing: border-box;
}

body {
  font-size:x-large;
  color: rgb(243, 255, 154);
  background-color: rgb(65, 5, 40);
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

h1 {
  margin-top: 0;
  font-size: 22px;
}

h2 {
 
  margin-top: 0;
  font-size: 20px;
}

h3 {
  color: rgb(215, 255, 246);
  margin-top: 0;
  font-size: 18px;
}

h4 {
 
  margin-top: 0;
  font-size: 16px;
}

h5 {
  
  margin-top: 0;
  font-size: 14px;
}

h6 {
  
  margin-top: 0;
  font-size: 12px;
}

code {
  font-size: 1.2em;
}

ul {
  
  padding-left: 20px;
}

.square {
  background: rgb(4, 176, 67);
  border: 2px solid rgb(119, 255, 107);
  float:left;
  font-size: 24px;
  color: rgb(255, 255, 255); 
  font-weight: bold;
  line-height: 50px;
  height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
}

.board-row:after {
  
  clear: both;
  content: '';
  display: table;
}

.status {
  
  margin-bottom: 10px;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  color: rgb(129, 238, 252);
  font-family:monospace;
  margin-left: 100px;
}
button{
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size:large;
  background-color:deeppink ;
  color:azure;
  
}
